import React, { useState } from "react";
import ReactDOM from "react-dom";
import "../node_modules/bootswatch/dist/lux/bootstrap.css";
import "./styles.css";
import Particles from "react-particles-js";

function App() {
  const particlesOptions = {
    particles: {
      number: {
        value: 80,
        density: {
          enable: true,
          value_area: 800
        }
      },
      color: {
        value: "#000000"
      },
      shape: {
        type: "circle",
        stroke: {
          width: 0,
          color: "#000000"
        },
        polygon: {
          nb_sides: 5
        },
        image: {
          src: "img/github.svg",
          width: 100,
          height: 100
        }
      },
      opacity: {
        value: 0.5,
        random: false,
        anim: {
          enable: false,
          speed: 1,
          opacity_min: 0.1,
          sync: false
        }
      },
      size: {
        value: 10,
        random: true,
        anim: {
          enable: false,
          speed: 80,
          size_min: 0.1,
          sync: false
        }
      },
      line_linked: {
        enable: true,
        distance: 300,
        color: "#ffffff",
        opacity: 0.4,
        width: 2
      },
      move: {
        enable: true,
        speed: 12,
        direction: "none",
        random: false,
        straight: false,
        out_mode: "out",
        bounce: false,
        attract: {
          enable: false,
          rotateX: 600,
          rotateY: 1200
        }
      }
    },
    interactivity: {
      detect_on: "canvas",
      events: {
        onhover: {
          enable: false,
          mode: "repulse"
        },
        onclick: {
          enable: false,
          mode: "push"
        },
        resize: true
      },
      modes: {
        grab: {
          distance: 800,
          line_linked: {
            opacity: 1
          }
        },
        bubble: {
          distance: 800,
          size: 20,
          duration: 2,
          opacity: 0.8,
          speed: 3
        },
        repulse: {
          distance: 400,
          duration: 0.4
        },
        push: {
          particles_nb: 4
        },
        remove: {
          particles_nb: 2
        }
      }
    },
    retina_detect: true
  };

  const [users, setUser] = useState(0);
  const [comp, setComputer] = useState(0);
  const [winner, setWinner] = useState("");
  const [choice, setChoice] = useState([
    {
      User: "",
      Computer: ""
    }
  ]);

  const rock = "https://img.icons8.com/color/180/000000/hand-rock.png";
  const paper = "https://img.icons8.com/color/180/000000/hand.png";
  const scissors = "https://img.icons8.com/color/180/000000/hand-scissors.png";

  const handleGame = e => {
    const user = e.target.id;
    const computer = ["rock", "paper", "scissors"];
    let rand = Math.floor(Math.random() * computer.length);
    let compute = computer[rand];
    setChoice([{ User: user, Computer: compute }]);
    console.log(e.target.id, compute);
    if (
      (user === "rock" && compute === "scissors") ||
      (user === "scissors" && compute === "paper") ||
      (user === "paper" && compute === "rock")
    ) {
      setUser(users + 1);
      setWinner("User Wins");
    } else if (
      (compute === "rock" && user === "scissors") ||
      (compute === "scissors" && user === "paper") ||
      (compute === "paper" && user === "rock")
    ) {
      setComputer(comp + 1);
      setWinner("Computer Wins");
    } else setWinner("Tie");

    e.preventDefault();
  };

  const handleReset = e => {
    setUser(0);
    setComputer(0);
    setWinner("");
    console.clear();
    e.preventDefault();
  };

  return (
    <div className="p-2">
      <Particles className="particles" params={particlesOptions} />
      <div className="container text-center p-2 card shadow inherit">
        <div className="p-2 ">
          <h3 className="my-3">Rock Paper Scissors</h3>
          <span className="badge badge-pill badge-primary py-2 px-3 m-2 shadow">
            Player : {users}
          </span>
          <span className="badge badge-pill badge-primary py-2 px-3 m-2 shadow">
            Computer : {comp}
          </span>
          <span
            className="badge badge-pill badge-primary py-2 px-3 m-2 reset shadow"
            onClick={handleReset}
          >
            Restart Game
          </span>
        </div>
        <h6 className="my-2">Make your selection</h6>
        <div className="row mb-4">
          <div className="col-4">
            <img
              id="rock"
              src={rock}
              alt="Hand-Rock"
              className="img-fluid"
              onClick={handleGame}
            />
          </div>
          <div className="col-4">
            <img
              id="scissors"
              src={scissors}
              alt="Hand-Scissors"
              className="img-fluid"
              onClick={handleGame}
            />
          </div>
          <div className="col-4">
            <img
              id="paper"
              src={paper}
              alt="Hand-Paper"
              className="img-fluid"
              onClick={handleGame}
            />
          </div>
          <div className="mx-auto">
            {choice.map((ch, index) => (
              <div key={index}>
                {winner && (
                  <div className="p-2">
                    <h6 className="m-2">
                      User selected{" "}
                      <span className="badge badge-pill badge-success py-1 px-3 m-1">
                        {ch.User}
                      </span>{" "}
                      <br /> Computer selected{" "}
                      <span className="badge badge-pill badge-warning py-1 px-3 m-1">
                        {ch.Computer}
                      </span>
                    </h6>
                    <h6>
                      result :
                      <span className="badge badge-pill badge-info py-1 px-3 mx-1  text-center">
                        {winner}
                      </span>
                    </h6>
                  </div>
                )}
              </div>
            ))}
          </div>
        </div>
      </div>
    </div>
  );
}

const rootElement = document.getElementById("root");
ReactDOM.render(<App />, rootElement);
